.hero-wrapper {
  position: relative;
  min-height: 658px;
  background: conic-gradient(from 90deg at 16.03% 39.06%, #B80027, #003D85 62deg, #B80027 335deg);

  @include vp-1199 {
    min-height: 554px;
  }

  @include vp-767 {
    min-height: 491px;
  }
}

.hero {
  left: 50%;
}


.hero__text {
  position: relative;
  width: 377px;
  margin-top: 105px;
  margin-left: 50%;
  transform: translateX(-558px);
  font-weight: 600;
  font-size: 36px;
  line-height: normal;
  font-family: $ff-alumni;
  color: $color-default-white;
  text-align: end;

  @include vp-1199 {
    width: 324px;
    margin-top: 72px;
    transform: translateX(-336px);
  }

  @include vp-767 {
    width: 291px;
    margin-top: 65px;
    transform: translateX(-144px);
  }
}

.hero__text span {
  margin-right: 55px;
  font-size: 42px;
  letter-spacing: 1.44px;
  text-transform: uppercase;

  @include vp-1199 {
    margin-right: 7px;
    font-size: 30px;
    letter-spacing: 1.02px;
  }

  @include vp-767 {
    margin-right: 6px;
    font-size: 26px;
    letter-spacing: 0.84px;
  }
}

.hero__text h2 {
  margin-top: 10px;
  margin-bottom: 0;
  font-size: 96px;
  letter-spacing: 4.0px;
  text-transform: uppercase;

  @include vp-1199 {
    margin-top: 5px;
    font-size: 72px;
    text-transform: none;
  }

  @include vp-767 {
    margin-top: 6px;
  }
}

.hero__list {
  position: absolute;
  top: 176px;
  left: -39px;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  font-family: $ff-roboto;
  list-style: none;

  @include vp-1199 {
    top: 126px;
    left: -40px;
  }

  @include vp-767 {
    top: 117px;
  }
}

.hero__btn {
  margin-top: 44px;
  margin-left: 50%;
  transform: translateX(-418px);
  padding: 20px 54px;
  color: $color-default-white;
  background-color: $color-transparent;
  border: 4px solid $color-default-white;

  @include vp-1199 {
    margin-top: 35px;
    transform: translateX(-198px);
    padding: 20px 29px;
  }

  @include vp-767 {
    width: 290px;
    margin-top: 23px;
    transform: translateX(-145px);
    padding: 20px 82px;
  }
}

.hero__photo {
  position: absolute;
  display: block;
  top: 50px;
  left: 50%;
  transform: translateX(-122px);

  @include vp-1199 {
    top: 82px;
    left: 50%;
    transform: translateX(16px);
  }

  @include vp-767 {
    display: none;
  }
}
